'use client';
import { useState, useEffect, useRef, useCallback, useMemo } from 'react';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { RecentTrade, BYBIT_WEBSOCKET_URL, DEFAULT_REQUEST_TIMEOUT } from '@/lib/bybit-api';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Skeleton } from '@/components/ui/skeleton';
import { format } from 'date-fns';
import { cn } from '@/lib/utils';
import { Button } from '@/components/ui/button';
import { RefreshCw, Wifi, WifiOff, AlertCircle } from 'lucide-react';

const MAX_VISIBLE_TRADES = 50;
const LOADING_ROW_COUNT = 15;
const NEW_TRADE_HIGHLIGHT_DURATION = 1500;
const MAX_RETRY_ATTEMPTS = 5;
const RETRY_DELAY_BASE = 2000; // 2 seconds

const formatTime = (timestamp: string | number | undefined): string => {
  if (timestamp === undefined || timestamp === null) return '...';
  try {
    const numericTimestamp = Number(timestamp);
    if (isNaN(numericTimestamp) || !isFinite(numericTimestamp) || numericTimestamp <= 0) return 'Invalid Time';
    return format(new Date(numericTimestamp), 'HH:mm:ss');
  } catch (e) {
    console.error("Error formatting time:", e);
    return 'Invalid Time';
  }
};

const formatAmount = (amountStr: string | undefined): string => {
    if (!amountStr) return '0.000';
    const amount = parseFloat(amountStr);
    if (isNaN(amount)) return '0.000';
    if (amount >= 1_000_000) return `${(amount / 1_000_000).toFixed(2)}M`;
    if (amount >= 1000) return `${(amount / 1000).toFixed(2)}K`;
    if (amount < 0.01 && amount > 0) return amount.toFixed(5);
    if (amount < 0.0001 && amount > 0) return amount.toFixed(6);
    return amount.toFixed(3);
};

const LoadingRows = ({ count = LOADING_ROW_COUNT }) => (
  Array.from({ length: count }).map((_, i) => (
      <TableRow key={i} className="text-xs" aria-hidden="true">
          <TableCell className="p-2"><Skeleton className="h-4 w-full" /></TableCell>
          <TableCell className="p-2"><Skeleton className="h-4 w-full" /></TableCell>
          <TableCell className="p-2"><Skeleton className="h-4 w-full" /></TableCell>
      </TableRow>
  ))
);

interface RecentTradesProps {
  symbol: string;
  className?: string;
}

export function RecentTrades({ symbol, className }: RecentTradesProps) {
  const [trades, setTrades] = useState<RecentTrade[]>([]);
  const [newTradeIds, setNewTradeIds] = useState<Set<string>>(new Set());
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [wsStatus, setWsStatus] = useState<'connecting' | 'connected' | 'reconnecting' | 'disconnected'>('connecting');
  const [retryCount, setRetryCount] = useState(0);
  
  const wsRef = useRef<WebSocket | null>(null);
  const timeoutIds = useRef<NodeJS.Timeout[]>([]);
  const isMounted = useRef(true);
  const scrollAreaRef = useRef<HTMLDivElement>(null);

  const cleanup = useCallback(() => {
    wsRef.current?.close();
    wsRef.current = null;
    timeoutIds.current.forEach(clearTimeout);
    timeoutIds.current = [];
  }, []);

  const connectWebSocket = useCallback(() => {
    if (!symbol || !isMounted.current) return;

    cleanup();
    setWsStatus(retryCount > 0 ? 'reconnecting' : 'connecting');
    if (retryCount === 0) {
        setIsLoading(true);
        setTrades([]); // Clear trades on first connection attempt
    }

    const ws = new WebSocket(BYBIT_WEBSOCKET_URL);
    wsRef.current = ws;

    ws.onopen = () => {
      if (wsRef.current !== ws) return;
      console.log(`WebSocket connected for ${symbol}`);
      setWsStatus('connected');
      setError(null);
      setRetryCount(0);
      ws.send(JSON.stringify({ op: "subscribe", args: [`publicTrade.${symbol}`] }));
      
      // Add a timeout to handle cases where connection is open but no data is received
      const connectionTimeout = setTimeout(() => {
        if (wsRef.current === ws && trades.length === 0 && isMounted.current) {
          setError('Connection timed out. No data received.');
          ws.close();
        }
      }, DEFAULT_REQUEST_TIMEOUT);
      timeoutIds.current.push(connectionTimeout);
    };

    ws.onmessage = (event) => {
      if (!isMounted.current) return;
      const data = JSON.parse(event.data);
      
      if (data.topic && data.topic.startsWith('publicTrade') && Array.isArray(data.data)) {
        const newTrades = data.data.map(t => ({
            execId: t.i,
            price: t.p,
            qty: t.v,
            side: t.S,
            execTime: t.T,
            isBlockTrade: t.m,
        })) as RecentTrade[];
        
        setTrades(prev => [ ...newTrades, ...prev].slice(0, MAX_VISIBLE_TRADES));
        const newIds = new Set(newTrades.map(t => t.execId));
        setNewTradeIds(newIds);

        if (isLoading) setIsLoading(false);
        
        const highlightTimeout = setTimeout(() => {
            if(isMounted.current) setNewTradeIds(current => {
                const updated = new Set(current);
                newIds.forEach(id => updated.delete(id));
                return updated;
            });
        }, NEW_TRADE_HIGHLIGHT_DURATION);
        timeoutIds.current.push(highlightTimeout);
      }
    };

    ws.onerror = (event) => {
      if (wsRef.current !== ws) return;
      console.error("WebSocket error:", event);
      if (isMounted.current) {
        setError('WebSocket connection error.');
      }
    };

    ws.onclose = () => {
      if (wsRef.current !== ws || !isMounted.current) return;
      console.log(`WebSocket disconnected for ${symbol}`);
      if (retryCount < MAX_RETRY_ATTEMPTS) {
        const delay = RETRY_DELAY_BASE * Math.pow(2, retryCount);
        console.log(`Attempting to reconnect in ${delay}ms (attempt ${retryCount + 1})`);
        const reconnectTimeout = setTimeout(() => {
            if(isMounted.current) {
                setRetryCount(prev => prev + 1);
                connectWebSocket();
            }
        }, delay);
        timeoutIds.current.push(reconnectTimeout);
        setWsStatus('reconnecting');
      } else {
        setError(`Failed to connect after ${MAX_RETRY_ATTEMPTS} attempts. Please refresh manually.`);
        setWsStatus('disconnected');
        setIsLoading(false);
      }
    };
  }, [symbol, retryCount, trades.length, isLoading, cleanup]);

  useEffect(() => {
    isMounted.current = true;
    connectWebSocket();

    return () => {
      isMounted.current = false;
      cleanup();
    };
  }, [symbol]);

  const handleManualReconnect = useCallback(() => {
    setRetryCount(0);
    setError(null);
    connectWebSocket();
  }, [connectWebSocket]);

  const renderContent = useMemo(() => {
    if (isLoading) {
      return <LoadingRows />;
    }
    if (error && trades.length === 0) {
      return (
        <TableRow>
          <TableCell colSpan={3} className="text-center p-4" role="alert">
            <div className="flex flex-col items-center space-y-2">
              <AlertCircle className="h-5 w-5 text-red-500" />
              <p className="text-sm text-red-500">{error}</p>
              <Button onClick={handleManualReconnect} variant="outline" size="sm">
                <RefreshCw className="h-4 w-4 mr-1" />
                Reconnect
              </Button>
            </div>
          </TableCell>
        </TableRow>
      );
    }
    if (trades.length > 0) {
      return trades.map((trade) => {
        const isNew = newTradeIds.has(trade.execId);
        return (
            <TableRow 
                key={trade.execId} 
                className={cn(
                    "text-xs font-mono transition-all duration-1000",
                    isNew && trade.side === 'Buy' && 'bg-green-500/30',
                    isNew && trade.side === 'Sell' && 'bg-red-500/30'
                )}
            >
                <TableCell className="p-2">{formatTime(trade.execTime)}</TableCell>
                <TableCell className={`p-2 font-medium ${trade.side === 'Buy' ? 'text-chart-2' : 'text-chart-5'}`}>
                  {parseFloat(trade.price).toFixed(2)}
                </TableCell>
                <TableCell className="p-2 text-right">{formatAmount(trade.qty)}</TableCell>
            </TableRow>
        );
      });
    }
    return (
        <TableRow>
          <TableCell colSpan={3} className="text-center text-muted-foreground p-4">No trades to display.</TableCell>
        </TableRow>
    );
  }, [isLoading, error, trades, newTradeIds, handleManualReconnect]);

  const WsStatusIndicator = () => {
    switch (wsStatus) {
        case 'connected': return <Wifi className="h-4 w-4 text-green-500" title="Connected" />;
        case 'connecting':
        case 'reconnecting': return <RefreshCw className="h-4 w-4 text-yellow-500 animate-spin" title={wsStatus === 'connecting' ? 'Connecting...': 'Reconnecting...'} />;
        case 'disconnected':
        default: return <WifiOff className="h-4 w-4 text-red-500" title="Disconnected" />;
    }
  }

  return (
    <Card className={cn("", className)}>
      <CardHeader className="pb-3">
        <CardTitle className="flex items-center justify-between">
          <div className="flex items-center space-x-2">
            <span>Recent Trades</span>
          </div>
          <div className="flex items-center space-x-2">
            {wsStatus === 'disconnected' && (
                <Button variant="ghost" size="sm" onClick={handleManualReconnect} className="h-8 w-8 p-0" title="Reconnect">
                    <RefreshCw className="h-4 w-4"/>
                </Button>
            )}
            <WsStatusIndicator />
          </div>
        </CardTitle>
      </CardHeader>
      <CardContent className="p-0">
        <ScrollArea className="h-[200px]" ref={scrollAreaRef}>
          <Table>
            <TableHeader className="sticky top-0 bg-background">
              <TableRow className="text-xs">
                <TableHead className="p-2 w-[33%]">Time</TableHead>
                <TableHead className="p-2 w-[33%]">Price (USDT)</TableHead>
                <TableHead className="p-2 text-right w-[33%]">Amount</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {renderContent}
            </TableBody>
          </Table>
        </ScrollArea>
      </CardContent>
    </Card>
  );
}
