'use client';
import { useState, useEffect, useRef, useCallback } from 'react';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Skeleton } from '@/components/ui/skeleton';
import { type VolumePressureData } from '@/lib/actions';
import { TrendingUp, TrendingDown, Scale, RefreshCw, Wifi, WifiOff } from 'lucide-react';
import { cn } from '@/lib/utils';
import { Button } from '@/components/ui/button';

// Constants
const REFRESH_INTERVAL = 3000; // 3 seconds
const MAX_RETRY_ATTEMPTS = 3;
const RETRY_DELAY_MULTIPLIER = 1.5;
const INITIAL_RETRY_DELAY = 1000;

interface VolumePressureProps {
  symbol: string;
}

const formatVolume = (num: number | undefined, symbol: string): string => {
  if (num === undefined || isNaN(num)) return '0';
  
  const baseSymbol = symbol.replace('USDT', '');
  
  if (num >= 1_000_000_000) {
    return `${(num / 1_000_000_000).toFixed(2)}B ${baseSymbol}`;
  }
  if (num >= 1_000_000) {
    return `${(num / 1_000_000).toFixed(2)}M ${baseSymbol}`;
  }
  if (num >= 1_000) {
    return `${(num / 1_000).toFixed(1)}K ${baseSymbol}`;
  }
  if (num < 0.01 && num > 0) {
    return `${num.toFixed(5)} ${baseSymbol}`;
  }
  return `${num.toFixed(2)} ${baseSymbol}`;
};

export function VolumePressure({ symbol }: VolumePressureProps) {
  const [data, setData] = useState<VolumePressureData | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [retryCount, setRetryCount] = useState(0);
  const [isOnline, setIsOnline] = useState(true);
  const [lastUpdated, setLastUpdated] = useState<Date | null>(null);
  const [isRefreshing, setIsRefreshing] = useState(false);
  
  const intervalId = useRef<NodeJS.Timeout | null>(null);
  const timeoutIds = useRef<NodeJS.Timeout[]>([]);
  const isMounted = useRef(true);
  const abortControllerRef = useRef<AbortController | null>(null);

  const cleanup = useCallback(() => {
    if (intervalId.current) clearInterval(intervalId.current);
    timeoutIds.current.forEach(clearTimeout);
    abortControllerRef.current?.abort();
  }, []);

  const fetchData = useCallback(async (isInitialLoad = false, manualRefresh = false) => {
    if (!isMounted.current || (!isOnline && !manualRefresh)) return;

    if (manualRefresh) setIsRefreshing(true);
    else if (isInitialLoad) {
      setIsLoading(true);
      setError(null);
      setRetryCount(0);
    }
    
    abortControllerRef.current?.abort();
    abortControllerRef.current = new AbortController();

    try {
      const response = await fetch(`/api/trades?symbol=${symbol}`, {
        signal: abortControllerRef.current.signal,
      });

      if (!response.ok) {
        throw new Error(`API request failed with status ${response.status}`);
      }

      const responseData = await response.json();
      const pressureData = responseData.volumePressure as VolumePressureData;
      
      if (!isMounted.current) return;
      
      if (pressureData) {
        setData(pressureData);
        setLastUpdated(new Date());
        setError(null);
        setRetryCount(0);
      } else {
        throw new Error('No volume data received in API response.');
      }
    } catch (e) {
      if (!isMounted.current) return;
      const errorMessage = e instanceof Error ? e.message : 'An unknown error occurred.';
      if ((e as Error).name === 'AbortError') {
        console.log('Request aborted for volume pressure.');
        return;
      }
      
      console.error(`Failed to fetch volume pressure for ${symbol}:`, e);
      if (isInitialLoad || manualRefresh) {
        setError(errorMessage);
      }
      
      if (retryCount < MAX_RETRY_ATTEMPTS && isOnline) {
        const delay = INITIAL_RETRY_DELAY * Math.pow(RETRY_DELAY_MULTIPLIER, retryCount);
        setRetryCount(prev => prev + 1);
        
        const retryTimeoutId = setTimeout(() => {
          if (isMounted.current) fetchData(false);
        }, delay);
        timeoutIds.current.push(retryTimeoutId);
      } else if (retryCount >= MAX_RETRY_ATTEMPTS) {
        setError(`Failed to fetch data after ${MAX_RETRY_ATTEMPTS} attempts.`);
      }
    } finally {
      if (isMounted.current) {
        if (manualRefresh) setIsRefreshing(false);
        else if (isInitialLoad) setIsLoading(false);
      }
    }
  }, [symbol, retryCount, isOnline]);
  
  useEffect(() => {
    isMounted.current = true;
    const handleOnline = () => { setIsOnline(true); setError(null); fetchData(true); };
    const handleOffline = () => { setIsOnline(false); setError('Network connection lost.'); cleanup(); };
    
    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);
    
    fetchData(true);

    return () => {
      isMounted.current = false;
      cleanup();
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, [symbol]);

  useEffect(() => {
    cleanup();
    if (isOnline) {
      intervalId.current = setInterval(() => fetchData(false), REFRESH_INTERVAL);
    }
    return () => cleanup();
  }, [isOnline, fetchData, cleanup]);

  const handleManualRefresh = useCallback(() => {
    fetchData(false, true);
  }, [fetchData]);

  const formatLastUpdated = useCallback((date: Date | null): string => {
    if (!date) return '';
    return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit', second: '2-digit' });
  }, []);

  const renderContent = () => {
    if (isLoading) {
      return (
        <div className="space-y-4">
          <Skeleton className="h-6 w-3/4 mx-auto" />
          <Skeleton className="h-4 w-full" />
          <Skeleton className="h-6 w-1/2 mx-auto" />
        </div>
      );
    }
    
    if (error && !data) {
      return (
        <div className="text-center space-y-3">
          <p className="text-sm text-chart-5">{error}</p>
          <Button 
            onClick={handleManualRefresh} 
            variant="outline" 
            size="sm"
            disabled={isRefreshing}
          >
            <RefreshCw className={cn("h-4 w-4 mr-1", isRefreshing && "animate-spin")} />
            Retry
          </Button>
        </div>
      );
    }
    
    if (data) {
      const buyPressureHigher = data.buyPercentage > data.sellPercentage;
      
      return (
        <div className="space-y-3">
          <div className="flex justify-between items-center text-sm font-medium">
            <div className={cn("flex items-center gap-2", buyPressureHigher ? "text-chart-2 font-semibold" : "text-muted-foreground")}>
              <TrendingUp />
              <span>Buy Pressure</span>
            </div>
            <div className={cn("flex items-center gap-2", !buyPressureHigher ? "text-chart-5 font-semibold" : "text-muted-foreground")}>
              <span>Sell Pressure</span>
              <TrendingDown />
            </div>
          </div>
          
          <div className="relative flex h-4 w-full rounded-full overflow-hidden bg-muted">
            <div className={cn("h-full transition-all duration-500 ease-out", buyPressureHigher ? "bg-chart-2" : "bg-chart-2/70")} style={{ width: `${data.buyPercentage}%` }}/>
            <div className={cn("h-full transition-all duration-500 ease-out", !buyPressureHigher ? "bg-chart-5" : "bg-chart-5/70")} style={{ width: `${data.sellPercentage}%` }} />
            <div className="absolute top-0 left-1/2 transform -translate-x-1/2 w-0.5 h-full bg-background/50" />
          </div>
          
          <div className="flex justify-between items-center text-xs font-mono">
            <span className={cn(buyPressureHigher && "font-semibold")}>
              {data.buyPercentage.toFixed(2)}%
            </span>
            <span className={cn(!buyPressureHigher && "font-semibold text-chart-5")}>
              {data.sellPercentage.toFixed(2)}%
            </span>
          </div>
          
          <div className="text-center pt-2 border-t border-border">
            <div className="flex items-center justify-between mb-1">
              <p className="text-sm font-semibold text-muted-foreground flex items-center justify-center gap-2">
                <Scale className="h-4 w-4" /> Total Volume
              </p>
              <Button onClick={handleManualRefresh} variant="ghost" size="sm" className="h-6 w-6 p-0" disabled={isRefreshing} title="Refresh data">
                <RefreshCw className={cn("h-3 w-3", isRefreshing && "animate-spin")} />
              </Button>
            </div>
            <p className="text-lg font-bold font-mono">
              {formatVolume(data.totalVolume, symbol)}
            </p>
            {lastUpdated && (
              <p className="text-xs text-muted-foreground mt-1">
                Updated: {formatLastUpdated(lastUpdated)}
              </p>
            )}
          </div>
        </div>
      );
    }
    
    return (
      <div className="text-center text-muted-foreground space-y-3">
        <p>Waiting for trade data...</p>
        <Button onClick={handleManualRefresh} variant="outline" size="sm" disabled={isRefreshing}>
          <RefreshCw className={cn("h-4 w-4 mr-1", isRefreshing && "animate-spin")} />
          Refresh
        </Button>
      </div>
    );
  };

  return (
    <Card>
      <CardHeader className="pb-3">
        <CardTitle className="flex items-center justify-between">
          <span>Volume Pressure</span>
          {isOnline ? <Wifi className="h-4 w-4 text-green-500" /> : <WifiOff className="h-4 w-4 text-red-500" />}
        </CardTitle>
      </CardHeader>
      <CardContent>
        {renderContent()}
      </CardContent>
    </Card>
  );
}
