
// Types
export type TickerInfo = {
  lastPrice: string;
  highPrice24h: string;
  lowPrice24h: string;
  turnover24h: string; // Volume in quote currency (USDT)
  volume24h: string; // Volume in base currency (e.g., BTC)
  price24hPcnt: string; // 24h price change percentage
};

export type OrderBookEntry = [string, string]; // [price, size]
export type OrderBook = {
  bids: OrderBookEntry[];
  asks: OrderBookEntry[];
  ts: string; // Orderbook timestamp
};

export type RecentTrade = {
  execId: string;
  execTime: string | number;
  price: string;
  qty: string;
  side: 'Buy' | 'Sell';
  isBlockTrade?: boolean; // Whether it's a block trade
};

export type KlineEntry = [
  string, // Start time
  string, // Open
  string, // High
  string, // Low
  string, // Close
  string, // Volume
  string, // Turnover
  string? // Optional: Confirm flag (1 if the kline is closed)
];

// Constants
const BYBIT_API_URL = 'https://api.bybit.com';
export const BYBIT_WEBSOCKET_URL = 'wss://stream.bybit.com/v5/public/spot';
export const DEFAULT_REQUEST_TIMEOUT = 10000; // 10 seconds
const MAX_RETRIES = 3;
const RETRY_DELAY_MS = 1000;

// API Response Types
interface ApiResponse<T> {
  retCode: number;
  retMsg: string;
  result: T;
  time: number;
}

// Enhanced fetch function with retry logic and timeout
async function fetchFromBybit<T>(
  endpoint: string, 
  params: Record<string, string> = {}, 
  options: RequestInit = {}
): Promise<T | null> {
  const url = new URL(endpoint, BYBIT_API_URL);
  url.search = new URLSearchParams(params).toString();
  
  const controller = new AbortController();
  const timeoutId = setTimeout(() => controller.abort(), DEFAULT_REQUEST_TIMEOUT);
  
  const fetchOptions: RequestInit = {
    cache: 'no-store',
    signal: options.signal || controller.signal, // Use provided signal or internal timeout signal
    headers: {
      'Content-Type': 'application/json',
    },
    ...options
  };

  let retryCount = 0;
  
  while (retryCount <= MAX_RETRIES) {
    try {
      const response = await fetch(url.toString(), fetchOptions);
      clearTimeout(timeoutId); // Clear timeout once response is received
      
      if (!response.ok) {
        throw new Error(`Bybit API error: ${response.status} ${response.statusText}`);
      }
      
      const data: ApiResponse<T> = await response.json();
      
      if (data.retCode !== 0) {
        throw new Error(`Bybit API error: ${data.retMsg} (Code: ${data.retCode})`);
      }
      
      return data.result;
    } catch (error) {
      clearTimeout(timeoutId);
      
      // Don't retry on abort errors or if we've reached max retries
      if (error instanceof Error && error.name === 'AbortError') {
        console.error(`Request to ${endpoint} timed out or was aborted`);
        return null;
      }
      
      if (retryCount === MAX_RETRIES) {
        console.error(`Failed to fetch from Bybit endpoint ${endpoint} after ${MAX_RETRIES} retries:`, error);
        return null;
      }
      
      // Exponential backoff
      const delay = RETRY_DELAY_MS * Math.pow(2, retryCount);
      console.warn(`Retry ${retryCount + 1}/${MAX_RETRIES} for ${endpoint} in ${delay}ms`);
      
      await new Promise(resolve => setTimeout(resolve, delay));
      retryCount++;
    }
  }
  
  return null;
}

// Get ticker information with enhanced error handling
export async function getTicker(symbol: string): Promise<TickerInfo | null> {
  try {
    const result = await fetchFromBybit<any>('/v5/market/tickers', { 
      category: 'spot', 
      symbol 
    });
    
    if (!result || !result.list || result.list.length === 0) {
      console.warn(`No ticker data found for symbol: ${symbol}`);
      return null;
    }
    
    // Extract and validate the ticker data
    const ticker = result.list[0];
    
    // Validate required fields
    const requiredFields = ['lastPrice', 'highPrice24h', 'lowPrice24h', 'turnover24h', 'volume24h'];
    for (const field of requiredFields) {
      if (ticker[field] === undefined || ticker[field] === null) {
        console.warn(`Missing required field ${field} in ticker data for ${symbol}`);
        return null;
      }
    }
    
    return {
      lastPrice: ticker.lastPrice,
      highPrice24h: ticker.highPrice24h,
      lowPrice24h: ticker.lowPrice24h,
      turnover24h: ticker.turnover24h,
      volume24h: ticker.volume24h,
      price24hPcnt: ticker.price24hPcnt || '0',
    };
  } catch (error) {
    console.error(`Error fetching ticker for ${symbol}:`, error);
    return null;
  }
}

// Get order book with timestamp and validation
export async function getOrderBook(symbol: string, limit: number = 20): Promise<OrderBook | null> {
  try {
    const result = await fetchFromBybit<any>('/v5/market/orderbook', { 
      category: 'spot', 
      symbol, 
      limit: limit.toString() 
    });
    
    if (!result) {
      console.warn(`No orderbook data found for symbol: ${symbol}`);
      return null;
    }
    
    // Validate the orderbook structure
    if (!result.b || !result.a || !Array.isArray(result.b) || !Array.isArray(result.a)) {
      console.warn(`Invalid orderbook structure for symbol: ${symbol}`);
      return null;
    }
    
    // Ensure bids and asks are properly formatted
    const bids = result.b.filter((entry: any) => 
      Array.isArray(entry) && entry.length === 2 && entry[0] && entry[1]
    ) as OrderBookEntry[];
    
    const asks = result.a.filter((entry: any) => 
      Array.isArray(entry) && entry.length === 2 && entry[0] && entry[1]
    ) as OrderBookEntry[];
    
    return {
      bids,
      asks,
      ts: result.ts || Date.now().toString(),
    };
  } catch (error) {
    console.error(`Error fetching orderbook for ${symbol}:`, error);
    return null;
  }
}

// Get recent trades with enhanced filtering
export async function getRecentTrades(
  symbol: string, 
  limit: number = 30, 
  options: RequestInit = {}
): Promise<RecentTrade[]> {
  try {
    const result = await fetchFromBybit<any>(
      '/v5/market/recent-trade', 
      { 
        category: 'spot', 
        symbol, 
        limit: limit.toString() 
      }, 
      options
    );
    
    if (!result || !result.list || !Array.isArray(result.list)) {
      return [];
    }
    
    // Filter and validate trade data
    return result.list
      .filter((trade: any) => {
        return trade.execId && trade.execTime && trade.price && trade.qty && 
               (trade.side === 'Buy' || trade.side === 'Sell');
      })
      .map((trade: any) => ({
        execId: trade.execId,
        execTime: trade.execTime,
        price: trade.price,
        qty: trade.qty,
        side: trade.side as 'Buy' | 'Sell',
        isBlockTrade: trade.isBlockTrade || false,
      }));
  } catch (error) {
    if (!(error instanceof Error && error.name === 'AbortError')) {
      console.error(`Error fetching recent trades for ${symbol}:`, error);
    }
    return [];
  }
}

// Get kline/candlestick data with interval mapping and validation
export async function getKline(
  symbol: string, 
  interval: string, 
  limit: number = 100
): Promise<KlineEntry[] | null> {
  try {
    // Map user-friendly intervals to Bybit format
    const intervalMap: Record<string, string> = {
      '1m': '1',
      '5m': '5',
      '15m': '15',
      '30m': '30',
      '1h': '60',
      '2h': '120',
      '4h': '240',
      '6h': '360',
      '12h': '720',
      '1d': 'D',
      '1w': 'W',
      '1M': 'M',
    };
    
    const bybitInterval = intervalMap[interval] || interval;
    
    const result = await fetchFromBybit<any>('/v5/market/kline', { 
      category: 'spot', 
      symbol, 
      interval: bybitInterval, 
      limit: limit.toString() 
    });
    
    if (!result || !result.list || !Array.isArray(result.list)) {
      console.warn(`No kline data found for symbol: ${symbol} with interval: ${interval}`);
      return null;
    }
    
    // Validate and format kline entries
    return result.list
      .filter((entry: any) => {
        return Array.isArray(entry) && entry.length >= 7 && 
               entry.every((val: any) => val !== null && val !== undefined);
      })
      .map((entry: any) => entry.slice(0, 7) as KlineEntry);
  } catch (error) {
    console.error(`Error fetching kline data for ${symbol} with interval ${interval}:`, error);
    return null;
  }
}

// New function to get multiple symbols data at once
export async function getTickers(symbols: string[]): Promise<Record<string, TickerInfo>> {
  const results: Record<string, TickerInfo> = {};
  
  // Use Promise.allSettled to handle partial failures
  const promises = symbols.map(async (symbol) => {
    const ticker = await getTicker(symbol);
    return { symbol, ticker };
  });
  
  const settledResults = await Promise.allSettled(promises);
  
  settledResults.forEach((result) => {
    if (result.status === 'fulfilled' && result.value.ticker) {
      const { symbol, ticker } = result.value;
      results[symbol] = ticker;
    }
  });
  
  return results;
}
