'use client';

import { useState } from 'react';
import { Card, CardHeader, CardTitle, CardContent, CardFooter } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Bot, Zap, TrendingUp, TrendingDown, CircleDot, Shield, BrainCircuit, BarChart } from 'lucide-react';
import { getAiTradingSignal, type AiSignalResponse } from '@/lib/actions';
import { Skeleton } from '@/components/ui/skeleton';
import { useToast } from '@/hooks/use-toast';
import { Badge } from '@/components/ui/badge';
import type { IndicatorSettings } from '@/lib/indicators';
import { Terminal } from '@/components/ui/terminal';

interface AiSignalProps {
  symbol: string;
  timeframe: string;
  indicatorSettings: IndicatorSettings;
}

const getConfidenceVariant = (level: 'High' | 'Medium' | 'Low' | undefined) => {
  switch (level) {
    case 'High':
      return 'default';
    case 'Medium':
      return 'secondary';
    case 'Low':
      return 'outline';
    default:
      return 'outline';
  }
}

const getSignalClassName = (signal: 'Buy' | 'Sell' | 'Hold' | undefined) => {
    switch(signal) {
        case 'Buy':
            return 'text-chart-2';
        case 'Sell':
            return 'text-chart-5';
        default:
            return 'text-muted-foreground';
    }
}

export function AiSignal({ symbol, timeframe, indicatorSettings }: AiSignalProps) {
  const [isLoading, setIsLoading] = useState(false);
  const [analysis, setAnalysis] = useState<AiSignalResponse | null>(null);
  const { toast } = useToast();

  const handleGenerateSignal = async () => {
    setIsLoading(true);
    setAnalysis(null);
    const result = await getAiTradingSignal(symbol, timeframe, indicatorSettings);
    if (result.success && result.analysis) {
      setAnalysis(result.analysis);
    } else {
      toast({
        variant: 'destructive',
        title: 'Error Generating Signal',
        description: result.error || 'The AI failed to generate a trading signal. Please try again.',
      });
    }
    setIsLoading(false);
  };
  
  const renderDetail = (icon: React.ReactNode, label: string, value: string | number | undefined, valueClass?: string) => (
    <div className="flex items-start gap-4">
        <div className="flex items-center gap-2 text-muted-foreground shrink-0">
            {icon}
            <span className="text-sm">{label}</span>
        </div>
        <div className={`text-sm font-semibold text-right flex-grow ${valueClass ?? ''}`}>
            {(value === undefined || value === null || isNaN(value as number))
                ? '-' 
                : typeof value === 'number' ? `$${value.toLocaleString()}` : value}
        </div>
    </div>
  )

  return (
    <Card>
      <CardHeader className="flex flex-row items-center justify-between">
        <div className="flex items-center gap-2">
          <Bot className="h-5 w-5" />
          <CardTitle>AI Trading Signal</CardTitle>
        </div>
        <Button onClick={handleGenerateSignal} disabled={isLoading} className="bg-accent hover:bg-accent/90 text-accent-foreground">
          <Zap className="mr-2 h-4 w-4" />
          {isLoading ? 'Generating...' : 'Generate Signal'}
        </Button>
      </CardHeader>
      <CardContent>
        {isLoading && (
          <div className="space-y-4">
            <Skeleton className="h-4 w-3/4" />
            <Skeleton className="h-4 w-full" />
            <Skeleton className="h-4 w-5/6" />
            <Skeleton className="h-4 w-1/2" />
          </div>
        )}
        {!isLoading && !analysis && (
          <div className="text-center text-muted-foreground py-8">
            <p>Click "Generate Signal" to get AI-powered market analysis.</p>
          </div>
        )}
        {analysis && (
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="space-y-4">
                    {renderDetail(<BarChart/>, "Current Price", analysis.currentPrice)}
                    {renderDetail(<CircleDot/>, "Entry Price", analysis.entryPrice)}
                    {renderDetail(<TrendingUp className="text-chart-2"/>, "Take Profit", analysis.takeProfit, "text-chart-2")}
                    {renderDetail(<TrendingDown className="text-chart-5"/>, "Stop Loss", analysis.stopLoss, "text-chart-5")}
                </div>
                <div className="space-y-4">
                    <div className="flex items-center justify-between">
                        <h4 className="text-lg font-semibold">Signal: <span className={getSignalClassName(analysis.signal)}>{analysis.signal || 'N/A'}</span></h4>
                        <Badge variant={getConfidenceVariant(analysis.confidenceLevel)}>
                            Confidence: {analysis.confidenceLevel || 'N/A'}
                        </Badge>
                    </div>
                    <div className="flex items-start gap-4">
                         <div className="flex items-center gap-2 text-muted-foreground shrink-0 mt-1">
                            <BrainCircuit />
                            <span className="text-sm">Reasoning</span>
                        </div>
                    </div>
                    <Terminal text={analysis.reasoning} />
                </div>
            </div>
        )}
      </CardContent>
      <CardFooter>
          <p className="text-xs text-muted-foreground">AI analysis is for informational purposes only and not financial advice.</p>
      </CardFooter>
    </Card>
  );
}
