'use client';

import { useState, useEffect, useRef, useCallback } from 'react';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { TrendingUp, TrendingDown, BarChart, AlertCircle, Repeat, Waves } from 'lucide-react';
import { BYBIT_WEBSOCKET_URL, TickerInfo } from '@/lib/bybit-api';
import { Skeleton } from '@/components/ui/skeleton';
import { cn } from '@/lib/utils';

interface MarketDataProps {
  symbol: string;
}

export function MarketData({ symbol }: MarketDataProps) {
  const [data, setData] = useState<Partial<TickerInfo>>({});
  const [priceChange, setPriceChange] = useState<'up' | 'down' | 'same'>('same');
  const [wsStatus, setWsStatus] = useState<'connecting' | 'connected' | 'reconnecting' | 'disconnected'>('connecting');
  const [error, setError] = useState<string | null>(null);
  const wsRef = useRef<WebSocket | null>(null);
  const retryTimeoutRef = useRef<NodeJS.Timeout | null>(null);
  const retryCount = useRef(0);

  const connectWebSocket = useCallback(() => {
    if (wsRef.current) {
      wsRef.current.close();
    }
    
    setWsStatus(retryCount.current > 0 ? 'reconnecting' : 'connecting');
    wsRef.current = new WebSocket(BYBIT_WEBSOCKET_URL);

    wsRef.current.onopen = () => {
      console.log(`Market data WebSocket connected for ${symbol}`);
      setWsStatus('connected');
      setError(null);
      retryCount.current = 0;
      wsRef.current?.send(JSON.stringify({ op: "subscribe", args: [`tickers.${symbol}`] }));
    };

    wsRef.current.onmessage = (event) => {
      const message = JSON.parse(event.data);
      if (message.topic === `tickers.${symbol}` && message.data) {
        setData(prevData => {
            const newData = {
                ...prevData,
                ...message.data,
            };
            
            if (prevData.lastPrice && newData.lastPrice) {
                const priceDiff = parseFloat(newData.lastPrice) - parseFloat(prevData.lastPrice);
                setPriceChange(priceDiff > 0 ? 'up' : priceDiff < 0 ? 'down' : 'same');
            } else {
                setPriceChange('same');
            }
            
            return newData;
        });
      }
    };

    wsRef.current.onerror = (err) => {
      console.error('WebSocket error:', err);
      setError('WebSocket connection failed.');
    };

    wsRef.current.onclose = () => {
      console.log('Market data WebSocket disconnected');
      if (retryCount.current < 5) {
        retryCount.current++;
        setWsStatus('reconnecting');
        retryTimeoutRef.current = setTimeout(connectWebSocket, 2000 * retryCount.current);
      } else {
        setWsStatus('disconnected');
        setError('Failed to reconnect to WebSocket.');
      }
    };
  }, [symbol]);

  useEffect(() => {
    connectWebSocket();
    return () => {
      if (wsRef.current) {
        wsRef.current.close();
      }
      if (retryTimeoutRef.current) {
        clearTimeout(retryTimeoutRef.current);
      }
    };
  }, [connectWebSocket]);

  const formatNumber = (numStr: string | undefined, precision = 2) => {
    if (!numStr) return '--';
    const num = parseFloat(numStr);
    if (isNaN(num)) return '--';
    return num.toLocaleString(undefined, { maximumFractionDigits: precision, minimumFractionDigits: precision });
  };

  const formatVolume = (numStr: string | undefined) => {
    if (!numStr) return '--';
    const num = parseFloat(numStr);
    if (isNaN(num)) return '--';
    if (num > 1_000_000_000) return `${(num / 1_000_000_000).toFixed(2)}B`;
    if (num > 1_000_000) return `${(num / 1_000_000).toFixed(2)}M`;
    if (num > 1_000) return `${(num / 1_000).toFixed(1)}K`;
    return num.toFixed(2);
  };

  const pricePcnt = data?.price24hPcnt ? parseFloat(data.price24hPcnt) : 0;
  const isPositive = pricePcnt >= 0;
  const priceColor = priceChange === 'up' ? 'text-chart-2' : priceChange === 'down' ? 'text-chart-5' : 'text-inherit';

  const renderCardContent = (title: string, value: string | undefined, formatter: (val: string | undefined) => string, icon: React.ReactNode, prefix = '', suffix = '', isLoading?: boolean) => {
    return (
        <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">{title}</CardTitle>
                {icon}
            </CardHeader>
            <CardContent>
                {isLoading ? <Skeleton className="h-8 w-3/4" /> : <div className="text-2xl font-bold">{prefix}{formatter(value)}{suffix}</div>}
            </CardContent>
        </Card>
    );
  }

  if (error && wsStatus === 'disconnected') {
      return (
          <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-5">
              <Card className="md:col-span-2 lg:col-span-5">
                  <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                      <CardTitle className="text-sm font-medium text-red-500">Connection Error</CardTitle>
                      <AlertCircle className="h-4 w-4 text-red-500" />
                  </CardHeader>
                  <CardContent>
                      <p className="text-sm text-red-500">{error}</p>
                      <button onClick={() => { retryCount.current = 0; connectWebSocket(); }} className="text-sm text-blue-500 underline mt-2">Retry</button>
                  </CardContent>
              </Card>
          </div>
      );
  }

  return (
    <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-5">
        <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">Last Price</CardTitle>
                <span className={cn('text-sm font-semibold', isPositive ? 'text-chart-2' : 'text-chart-5')}>
                    {isPositive ? '+' : ''}{(pricePcnt * 100).toFixed(2)}%
                </span>
            </CardHeader>
            <CardContent>
                {wsStatus === 'connecting' ? <Skeleton className="h-8 w-3/4" /> : (
                    <div className={cn("text-2xl font-bold transition-colors duration-200", priceColor)}>
                        ${formatNumber(data.lastPrice)}
                    </div>
                )}
            </CardContent>
        </Card>
      {renderCardContent('24h High', data.highPrice24h, formatNumber, <TrendingUp className="h-4 w-4 text-muted-foreground" />, '$','', wsStatus === 'connecting')}
      {renderCardContent('24h Low', data.lowPrice24h, formatNumber, <TrendingDown className="h-4 w-4 text-muted-foreground" />, '$','', wsStatus === 'connecting')}
      {renderCardContent('24h Volume', data.volume24h, formatVolume, <BarChart className="h-4 w-4 text-muted-foreground" />, '', ` ${symbol.replace('USDT', '')}`, wsStatus === 'connecting')}
      {renderCardContent('24h Turnover', data.turnover24h, formatVolume, <Waves className="h-4 w-4 text-muted-foreground" />, '$', ' USDT', wsStatus === 'connecting')}
    </div>
  );
}
